div.btns {
  position: relative;
  top: 2%;
  height: 50%;
}
div.btns button {
  position: relative;
  width: 25%;
  height: 20%;
}
div.btns button img {
  display: inline-block;
  height: 70%;
  transition: all 0.3s;
}
div.btns:active button {
  transform: scale(0.8);
}
div.btns:active button:active {
  z-index: 1;
  transform: scale(1.5);
}
div.btns:active button:active img {
  animation: jello 0.3s;
}