.error, .sorry {
  display: flex;
  align-items: center;
  justify-content: center;
  &.active{
    opacity: 0;
    transition: opacity 1s;
  }//&.active
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(#000, 0.8);
  img{
    max-width: 80%;
    max-height: 80%;
    animation: shake 0.5s 2;
  }
}