.error, .sorry {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}
.error.active, .sorry.active {
  opacity: 0;
  transition: opacity 1s;
}
.error img, .sorry img {
  max-width: 80%;
  max-height: 80%;
  animation: shake 0.5s 2;
}