div.output {
  position: relative;
  height: 40%;
  // background-color: rgb(61, 116, 116);
  &::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 120%;
    bottom: 0;
    background-image: url(../img/output.png);
    background-size: 100% 100%;
  }
  p{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 87%;
    height: 78%;
    box-sizing: border-box;
    padding: 5px;
    top: 11%;
    left: 6.5%;
    img{
      width: 10%;
      animation: pulse 0.3s;
    }
  }
}