body {
  background: url(../img/paper.jpg);
  background-size: cover;
}

section.app {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 95%;
  max-width: 400px;
  max-height: 600px;
}
section.app::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: url(../img/bg.png);
  background-size: 100% 100%;
  transform: scale(1.2);
}
section.app > * { /*title, btns, output*/
  position: relative;
}