div.btns {
  position: relative;
  top: 2%;
  height: 50%;
  
  button{
    position: relative;
    width: 25%;
    height: 20%;
    img{
      display: inline-block;
      height: 70%;
      transition: all 0.3s;
    }
  }//button
  //클릭모션
  &:active {
    button {
      transform: scale(0.8);
      &:active {
        z-index: 1;
        transform: scale(1.5);
        img{
          animation: jello 0.3s;
        }//img
      }//button
    }//button
  }//&:active
}